import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

const test = 'https://hyem00.github.io/site/dummy/';

export default new Vuex.Store({
  state: {
    //state  일종의변수
    api_url     : `${test}`,
    is_admin    : false,
    is_member   : false,
    token       : '',
    controller  : {
      login     : false,
      loading   : true,
      map       : true,
      video     : false,
      community : false,
      member    : false,
    },
    authority:{
      
    }
  },
  mutations: {
    //state  업데이트
    //commit 으로호출 
  },
  actions: {
    //mutations Or Ather function 호출용도
    //dispatch 으로호출
    async AJAX_FROM(context,data){
      const params = new URLSearchParams();
      const url = data.url
      let api_data;

      params.append('action','update')
      for(let key in data.data) 
        params.append(key,data.data[key]) 
      

      await axios.get(`${context.state.api_url}${url}`,params).then((req)=>{
          api_data = req;
      })

      return api_data;
    },

    async AJAX_SETTING_GET(context,token){
      const params = new URLSearchParams();
      context.state.controller.loading = true
      params.append('token',token)
      await axios.get(`${context.state.api_url}loading.json`,params).then((req)=>{
          req
          setTimeout(()=>{
            context.state.controller.loading = false
          },1500)
      });
    },


    FN_AUTO_NUMBER_VALUE(arr){
      const return_arr = new Array()
      if((typeof arr) == 'object'){
        for(let i = 0; i < arr.length;i++){
          return_arr.push(i);
        }
      }
      return return_arr
    },

  },
  modules: {
    //FromCheck  
  }
})
