<template>
<div id=makemap>
  <div id="map"></div>
  
</div>
</template>
<script>
/*
  <div v-show=false> 
     {{addr.lat}}
     {{addr.lon}}
  </div>
*/
export default {
    //props:['addr'],
    data:function(){
        return{
          addr:{
            lat:'37.564343',
            long:'126.947613'
          }
        }
    },
   mounted() {
        if (window.kakao && window.kakao.maps) {
            this.initMap();
        } else {
            const script = document.createElement('script');
            /* global kakao */
            script.onload = () => kakao.maps.load(this.initMap);
            script.src = 'https://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=e87b9842b0a2700a9beaca20846c93f8';
            document.head.appendChild(script);
        }
    },
   updated(){
        this.initMap();
    },
    methods: {
        initMap() {
            var container = document.getElementById('map');
            var options = {
              center: new kakao.maps.LatLng(this.addr.lat,this.addr.lon),
              level: 3
            };

            var map2 = new kakao.maps.Map(container, options);

            var markerPosition  = new kakao.maps.LatLng(this.addr.lat,this.addr.lon); 
            var marker = new kakao.maps.Marker({
                 position: markerPosition
            });
            marker.setMap(map2);

        }
    }
}

</script>
<style>
#map {
    background: green;
    width: 100%;
    height: 100%;
}

#makemap{
    width: 100%;
    height: 100%;
}
</style>